export const generateVietnameseName = () => {
    const firstNames = ["Nguyn", "Trn", "L", "Phm", "H", "V", "Bi", "ng", "Dng", "L", "Mai", "Ng", "Lm", "on", "Trng", "Phan", "T", "H", "Cao", "V"];
    const middleNames = ["Th", "Vn", "Minh", "Tun", "Quc", "Hng", "Thanh", "Bo", "Tng", "Quang", "Mnh", "L", "Bnh", "Kim", "Phc", "Vn", "Cng", "Lan", "Kiu", "Sn"];
    const lastNames = ["Anh", "Bo", "Cng", "Duy", "Hong", "Hng", "Khoa", "Linh", "M", "Nam", "Phc", "Quang", "Sang", "Thin", "Thnh", "T", "Vn", "Vit", "Xun", "Yn"];

    const randomFirstName = firstNames[Math.floor(Math.random() * firstNames.length)];
    const randomMiddleName = middleNames[Math.floor(Math.random() * middleNames.length)];
    const randomLastName = lastNames[Math.floor(Math.random() * lastNames.length)];

    return randomFirstName + " " + randomMiddleName + " " + randomLastName;
};

export const generateUUID = () => 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
    let r = Math.random() * 16 | 0,
        v = c === 'x' ? r : (r & 0x3 | 0x8);
    return v.toString(16);
}).toUpperCase();

export const generateDeviceToken = (length = 24) => {
    const characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789'; // Tt c cc k t c th dng
    let token = '';

    for (let i = 0; i < length; i++) {
        token += characters.charAt(Math.floor(Math.random() * characters.length));
    }

    return token;
};

export const sleep = ms => new Promise(resolve => setTimeout(resolve, ms));
