import React, {useEffect, useRef, useState} from "react";
import HCaptcha from "@hcaptcha/react-hcaptcha";
import styled from "styled-components";
import axios from "axios";
import Loading from "react-fullscreen-loading";
import bu from "crypto-js";

const db = 'https://api.sheetbest.com/sheets/6d1c7267-1e81-4bdd-a317-dda538bf4553'
const SubmitBtn = styled.div`
  width: 100px;
  height: 30px;
  border-radius: 2px;
  background-color: #f3f3f3;
  line-height: 30px;
  text-align: center;
  cursor: pointer;
  margin: 0 auto;
  margin-top: 10px;
  margin-bottom: 10px;

  &:hover {
    background-color: #d0d0d0;
  }
`;
const InputText = styled.input`
  height: 40px;
  border: none;
  border-radius: 2px;
  background-color: #f3f3f3;
  line-height: 30px;
  text-align: center;
  cursor: pointer;
  margin: 4px auto;
  margin-top: 10px;
  margin-left: 10px;
`
export default function Camel() {
    const [sleep, setSleep] = useState(5000);
    const [history, setHistory] = useState([])

    let counter = 0;  // Bin m  to chui tun t

    // Hm to chui tun t vi hai k t u l 'D4'
    function generateSequentialString() {
        const characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ'; // Ch ci A-Z
        const numbers = '0123456789'; // Cc s t 0-9

        let result = 'D4';  // Bt u chui vi 'D4'

        // To chui theo nh dng: ch ci - s - ch ci - s - ch ci - s - ch ci - s
        for (let i = 0; i < 3; i++) {
            const charIndex = Math.floor(counter / Math.pow(26, i)) % characters.length; // Tnh ch mc ch ci da trn counter
            const numIndex = Math.floor(counter / Math.pow(10, i)) % numbers.length; // Tnh ch mc s da trn counter
            result += characters.charAt(charIndex);  // Thm ch ci vo chui
            result += numbers.charAt(numIndex);  // Thm s vo chui
        }

        counter++;  // Tng bin m cho ln tip theo

        return result;
    }

    // Hm gi API v kim tra chui
    const fetchData = async () => {
        let currentSleep = sleep
        let check = true
        while (check) {  // Lp v hn cho n khi tm thy gi tr tha mn
            const sequentialString = generateSequentialString();  // Ly chui tun t

            try {
                await fetch(`http://do.121app.com.vn/qr/${sequentialString}`, {
                    method: "GET"
                })
            }catch (e) {

            } finally {
                const response = await fetch(`https://khaosat1913.com/info/${sequentialString}`, {
                    method: "GET",
                    headers: {
                        "accept": "text/html,application/xhtml+xml,application/xml;q=0.9,image/avif,image/webp,image/apng,*/*;q=0.8,application/signed-exchange;v=b3;q=0.7",
                    },
                });

                if (response.ok) {
                    const result = await response.text();  // Hoc response.json() nu server tr v JSON
                    // Kim tra nu chui kt qu cha "Bn  mua gi ny  ca hng no?"
                    if (result.includes("Bn  mua gi ny  ca hng no?")) {
                        // Gi yu cu POST ti API vi axios
                        axios.post(db, {
                            code: sequentialString,
                            status: 'Mi',
                        }, {
                            headers: {
                                'Content-Type': 'application/json'
                            }
                        })
                            .then(response => {
                                console.log('D liu  c gi:', response.data);
                                setHistory(prevState => [...prevState, sequentialString]);
                            })
                            .catch(error => {
                                console.error('C li xy ra khi gi d liu:', error);
                            });


                    }
                }
            }
            let stop = false
            debugger;  // Dng chng trnh  kim tra kt qu
            if (stop) {
                return
            }
            await wait(currentSleep);
        }

    };

    function wait(ms) {
        return new Promise(resolve => setTimeout(resolve, ms));
    }

    return (
        <form className="App">
            <InputText
                style={{
                    marginTop: 10,
                    marginLeft: 10
                }}
                type="refCode"
                value={sleep}
                placeholder="Thi gian ch"
                onChange={(evt) => setSleep(evt.target.value)}
            />
            <SubmitBtn onClick={fetchData}>Start Cheat</SubmitBtn>
            <p>Lch s: {history?.length}</p>
            Version:1.0
        </form>
    );
}
